import React, { useEffect, useState } from "react";
import { PlaceOrder } from "../../firebase";
import { store } from "../../store";
import { CheckoutItem } from "./ChekoutItem";

export const Checkout = () => {
  const [basketCount, setBasketCount] = useState([]);
  const [totalPrice, setTotalPrice] = useState(0);
  const [basketString, setBasketString] = useState("");
  const basketSelector = store.getState().basket;

  store.subscribe(() => setBasketCount(store.getState().basket));
  useEffect(() => {
    setBasketCount(basketSelector);
  }, []);
  useEffect(() => {
    let total = totalPrice;
    let basketSTR = basketString || "";

    basketCount.forEach(
      (item) => (total = total + parseFloat(item.price) * item.count)
    );
    basketCount.forEach(
      (item) =>
        (basketSTR =
          basketSTR + " " + item.titleru + " кол-во :" + item.count + ";")
    );

    setTotalPrice(total);
    setBasketString(basketSTR);
  }, [basketCount]);

  const onSubmit = async (e) => {
    e.preventDefault();

    const form = {};
    const formData = new FormData(e.target);
    // @ts-ignore
    for (const [key, value] of formData.entries()) {
      if (value) {
        form[key] = value;
      }
    }
    form.basket = basketString + ` Общая цена заказа: ${totalPrice}`;
    await PlaceOrder(form);
  };
  const currentLang = store.getState().lang;
  return (
    <div class="section-padding pb-0">
      <div class="container">
        <form onSubmit={onSubmit}>
          <div class="row">
            <div class="col-xl-7">
              <h4>
                {" "}
                {currentLang === "ru" ? "Детали заказа" : "Деталi замовлення"}
              </h4>
              <div class="row">
                <div class="form-group col-xl-12">
                  <label class="form-label">
                    {currentLang === "ru" ? "Полное имя" : "Повне ім'я"}
                    <span class="text-danger">*</span>
                  </label>
                  <input
                    type="text"
                    placeholder={
                      currentLang === "ru" ? "Полное имя" : "Повне ім'я"
                    }
                    name="name"
                    class="form-control"
                    required=""
                  />
                </div>

                <div class="form-group col-xl-12">
                  <label class="form-label">
                    {currentLang === "ru" ? "Город" : "Місто"}
                    <span class="text-danger">*</span>
                  </label>
                  <input
                    type="text"
                    placeholder={currentLang === "ru" ? "Город" : "Місто"}
                    name="town"
                    class="form-control"
                    required=""
                  />
                </div>
                <div class="form-group col-xl-6">
                  <label class="form-label">
                    {currentLang === "ru"
                      ? " Номер телефона"
                      : "Номер телефону"}
                    <span class="text-danger">*</span>
                  </label>
                  <input
                    type="text"
                    placeholder={
                      currentLang === "ru"
                        ? " Номер телефона"
                        : "Номер телефону"
                    }
                    name="phone"
                    class="form-control"
                    required=""
                  />
                </div>
                <div class="form-group col-xl-6">
                  <label class="form-label">
                    {currentLang === "ru"
                      ? " Удобный способ оплаты"
                      : "Зручний спосіб оплати"}
                    <span class="text-danger">*</span>
                  </label>
                  <input
                    type="text"
                    placeholder={
                      currentLang === "ru"
                        ? " Удобный способ оплаты"
                        : "Зручний спосіб оплати"
                    }
                    name="sposobOplaty"
                    class="form-control"
                    required=""
                  />
                </div>

                <div class="form-group col-xl-12">
                  <label class="form-label">
                    Служба доставки <span class="text-danger">*</span>
                  </label>
                  <select class="form-control">
                    <option value="">Служба доставки</option>
                    <option value="novaPoshta">Новая почта</option>
                    <option value="intaym">Интайм</option>
                    <option value="mist">МистЭкспресс</option>
                    <option value="mejdunarodnayaDostavka">
                      Международная доставка
                    </option>
                  </select>
                </div>
                <div class="form-group col-xl-6">
                  <label class="form-label">
                    {currentLang === "ru"
                      ? "Номер отделения службы доставки"
                      : "Номер відділення служби доставки"}
                    <span class="text-danger">*</span>
                  </label>
                  <input
                    type="text"
                    placeholder={
                      currentLang === "ru"
                        ? "Номер отделения службы доставки"
                        : "Номер відділення служби доставки"
                    }
                    name="novaPoshta"
                    class="form-control"
                    required=""
                  />
                </div>
                <div class="form-group col-xl-12 mb-0">
                  <label class="form-label">
                    {currentLang === "ru" ? "Комментарий" : "Коментарiй"}
                  </label>
                  <textarea
                    name="comment"
                    rows="5"
                    class="form-control"
                    placeholder={
                      currentLang === "ru" ? "Комментарий" : "Коментарiй"
                    }
                  ></textarea>
                </div>
              </div>
            </div>
            <div class="col-xl-5 checkout-billing mt-4 mt-xl-0">
              <div class="table-responsive-md">
                <table class="andro_responsive-table">
                  <thead>
                    <tr>
                      <th> Товар</th>
                      <th>{currentLang === "ru" ? "Кол-во" : "Кiль-сть"}</th>
                      <th>{currentLang === "ru" ? "Цена" : "Цiна"}</th>
                    </tr>
                  </thead>
                  <tbody>
                    {basketCount.map((item, index) => {
                      return <CheckoutItem key={index} data={item} />;
                    })}

                    <tr class="total">
                      <td>
                        <h6 class="mb-0">
                          {currentLang === "ru"
                            ? "Общая цена"
                            : "Загальна цiна"}
                        </h6>
                      </td>
                      <td></td>
                      <td>
                        <strong>{totalPrice} ГРН</strong>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
              {basketCount.length > 0 && (
                <button
                  onClick={() => {
                    window.localStorage.clear();
                    window.location.reload();
                  }}
                  type="submit"
                  class="btn btn-block"
                >
                  {currentLang === "ru" ? "Очистить корзину" : "Очистити кошик"}
                </button>
              )}
              <button type="submit" class="btn btn-block">
                {currentLang === "ru"
                  ? "Подтвердить заказ"
                  : "Пiдтвердити замовлення"}
              </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};
