import React, { useEffect, useState } from "react";
import Skeleton from "react-loading-skeleton";
import { useHistory, useParams } from "react-router";
import { getProducts } from "../../firebase";
import { addItem, deleteItem, store } from "../../store";
export const ProductDetails = () => {
  const [product, setProduct] = useState({});
  const [itemCount, setItemCount] = useState(0);
  const basketState = store.getState().basket;
  const { id } = useParams();
  const history = useHistory();
  useEffect(async () => {
    if (id) {
      const data = await getProducts(id);
      setProduct(data[0]);
    }
  }, []);
  useEffect(() => {
    const state = [...basketState];
    const item = state.find((item) => `:${item.id}` === id);
    item?.count > 0 && setItemCount(item.count);
  }, [basketState]);
  const addItemtoBasket = () => {
    product && addItem(product);
  };

  const hadnleAddProduct = () => {
    addItemtoBasket();
    setItemCount(itemCount + 1);
  };
  const handleRemoveProduct = () => {
    deleteItem(product.id);
    itemCount > 0 && setItemCount(itemCount - 1);
  };
  const currentLang = store.getState().lang;
  return (
    <>
      <div class="section-padding pb-0">
        <div class="container">
          <div class="row">
            <div class="col-lg-5">
              <div class="sigma_product-slider-1">
                <div class="item">
                  {product?.photo ? (
                    <img src={product.photo} alt="img" />
                  ) : (
                    <Skeleton height={300} />
                  )}
                </div>
              </div>
            </div>
            <div className="col-lg-7">
              <div className="shop-detail-content mt-5 mt-lg-0">
                <h3 className="product-title m-0">
                  {product && currentLang === "ru" ? (
                    product.titleru
                  ) : product && currentLang === "ua" ? (
                    product.titleua
                  ) : (
                    <Skeleton height={30} />
                  )}
                </h3>

                <div className="sigma_product-price">
                  {product?.price ? (
                    <span>{product.price} грн</span>
                  ) : (
                    <Skeleton height={80} />
                  )}
                  {product?.salePrice && <span>{product.salePrice} грн</span>}
                </div>
                <div className="shop-detail-inner">
                  <div className="availability-box">
                    <div className="d-inline-block other-info">
                      <h6>
                        {currentLang === "ru" ? "Наличие" : "Наявнiсть"}:
                        {product?.availability ? (
                          <span className="text-success ml-2">
                            {product.availability && "In Stock"}
                          </span>
                        ) : (
                          <Skeleton />
                        )}
                      </h6>
                    </div>
                    <div className="ml-2 d-inline-block other-info">
                      <h6>
                        SKU:
                        <span className="grey ml-2">{product.id}</span>
                      </h6>
                    </div>
                  </div>
                  <div className="short-descr">
                    {product?.descriptionru ? (
                      <p>
                        {currentLang === "ru"
                          ? product.descriptionru
                          : product.descriptionua}
                      </p>
                    ) : (
                      <Skeleton height={90} />
                    )}
                  </div>
                  <div className="quantity-cart d-sm-flex align-items-center">
                    {product?.price ? (
                      <div className="quantity-box">
                        <button
                          onClick={product?.price && handleRemoveProduct}
                          type="button"
                          className="minus-btn"
                        >
                          <i className="fa fa-minus"></i>
                        </button>
                        <input
                          type="text"
                          className="input-qty"
                          name="name"
                          value={itemCount}
                        />
                        <button
                          onClick={product?.price && hadnleAddProduct}
                          type="button"
                          class="plus-btn"
                        >
                          <i className="fa fa-plus"></i>
                        </button>
                      </div>
                    ) : (
                      <Skeleton height={50} width={170} />
                    )}
                    <div className="cart-btn">
                      {product?.price ? (
                        <button
                          className="btn"
                          onClick={() => history.push("/checkout")}
                        >
                          {currentLang === "ru" ? "В корзину" : "У кошик"}
                        </button>
                      ) : (
                        <Skeleton height={40} width={150} />
                      )}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};
